@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Mono:wght@300;400;600&display=swap');

* {
  padding: 0;
  outline: 0;
  border: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #ffffff;
  --color-bg-variant: #5151634e;
  --color-primary: #4241672b;
  --color-primary-variant: #3dabde;
  --color-white: rgb(0, 0, 0);
  --color-light: #000000;

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-ms: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Red Hat Mono', monospace;
  background: var(--color-bg);
  color: black;
  line-height: 1.7;
}

/* ====== General Styles ======= */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* ====== Media Queries (Medium/Tablet) ===== */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }
}

section {
  margin-top: 6rem;
  /* height: 100vh;  */
}

/* ====== Media Queries (Small/Phones) ===== */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-ms);
  }

  section {
    margin-top: 2rem;
  }
}
